<template>
  <div class="shape">
        <van-nav-bar
            title="选择体型"
            left-text="返回"
            left-arrow
            @click-left="$router.go(-1)"
        />
      <div class="wrap">
          <!-- <div class="choose_wrap">
              <div class="tag" v-if="choose.id">
                  <img :src="choose.url" alt="">
                  <p> {{choose.name}} </p>
              </div>
              <div class="clear" @click="clear">
                    <p>清除</p>
                    <van-icon name="cross" />
              </div>
          </div> -->
          <div class="list">
              <div v-if="$store.state.s_sex.status == 'm'">
                <div class="item" @click="chooseBtn_m(item)" v-for="item in list_m" :key="item.id" :class="item.id == choose.id ? 'active' : ''">
                    <img :src="item.url" alt="">
                    <p>{{item.title}}</p>
                </div>
              </div>
              <div v-if="$store.state.s_sex.status == 'f'">
                <div class="item"  @click="chooseBtn_f(item)" v-for="item in list_f" :key="item.id" :class="item.id == choose.id ? 'active' : ''">
                    <img :src="item.url" alt="">
                    <p>{{item.title}}</p>
                </div>
              </div>
          </div>
      </div>

      <div class="btn-wrap">
          <van-button type="primary" size="large" @click="submit" color="#2873ff">确定</van-button>
      </div>
  </div>
</template>
 
<script>
import { mapState, mapGetters } from "vuex";
export default {
  name: 'price',
  data () {
    return {
        choose: {
            // name: '胖',
            // id: '1',
            // url: 'https://www.baidu.com/img/bd_logo1.png'
        },

        list_m: [{
            id: '11',
            url: require('../assets/img/shape/m_s.png'),
            title: '消瘦'
        },{
            id: '12',
            url: require('../assets/img/shape/m_L.png'),
            title: '标准'
        },{
            id: '13',
            url: require('../assets/img/shape/m_XL_down.png'),
            title: '下身胖'
        },{
            id: '14',
            url: require('../assets/img/shape/m_XL_up.png'),
            title: '上身胖'
        },{
            id: '15',
            url: require('../assets/img/shape/m_XXL.png'),
            title: '微胖'
        },{
            id: '16',
            url: require('../assets/img/shape/m_XXXL.png'),
            title: '极胖'
        }],
        list_f: [{
            id: '21',
            url: require('../assets/img/shape/f_s.png'),
            title: '消瘦'
        },{
            id: '22',
            url: require('../assets/img/shape/f_L.png'),
            title: '标准'
        },{
            id: '23',
            url: require('../assets/img/shape/f_XL_down.png'),
            title: '下身胖'
        },{
            id: '24',
            url: require('../assets/img/shape/f_XL_up.png'),
            title: '上身胖'
        },{
            id: '25',
            url: require('../assets/img/shape/f_XXL.png'),
            title: '微胖'
        },{
            id: '26',
            url: require('../assets/img/shape/f_XXXL.png'),
            title: '极胖'
        }]
    }
  },
  computed: {
      ...mapState({
          s_sex: state => state.s_sex,
          s_shape: state => state.s_shape
      })
  },
  created() {
      console.log( this.s_shape )
      console.log( this.s_sex )
      switch(this.s_sex.status) {
          case 'm':
                this.choose = this.s_shape.m;
                break;
            case 'f':
                this.choose = this.s_shape.f;
                break;
      }
  },
  methods: {
    clear() {
        this.choose = {
            name: '',
            id: ''
        }
    },
    chooseBtn_m(val) {
        console.log( val )
        this.choose = val;
        // this.$store.state.s_shape.m = val;
    },
    chooseBtn_f(val) {
        console.log( val )
        this.choose = val;
        // this.$store.state.s_shape.f = val;
    },
    submit() {
        switch(this.s_sex.status) {
            case 'm':
                    this.$store.state.s_shape.m = this.choose;
                    break;
                case 'f':
                    this.$store.state.s_shape.f = this.choose;
                    break;
        }
        this.$store.state.s_shape.error = false;
        this.$store.state.s_shape.err_msg = '';
        console.log( this.choose )
        this.$router.go(-1);
    }
  }
}
</script>
 
<style scoped lang ="stylus">
.shape
    .wrap
        .choose_wrap
            position relative
            background #EEE
            height 110px
            .tag
                display inline-block
                padding 4px
                position absolute
                top 12px
                left 12px
                img 
                    height 60px
                p 
                    text-align center
            .clear
                display flex
                align-items center
                position absolute
                right 12px
                top 12px
                color #2873FF
                padding 4px
        .list
            padding 12px
            div
                display flex
                flex-wrap wrap
                .item
                    padding 6px 6px
                    width 33.3%
                    box-sizing border-box
                    p 
                        background #EEE
                        width 100%
                        text-align center
                        line-height 40px
                    img
                        height 65px
                        margin 0 auto
                .active
                    background rgba(40,115,255,0.1)
                    color #2873ff
                    p
                        background rgba(40,115,255,0.1)
    .btn-wrap
        position fixed
        bottom 0
        left 0
        width 100%
</style>